body.woocommerce-checkout #main.fc-main {
  border: 0;
}

body.theme-oceanwp div.woocommerce form .form-row.woocommerce-validated:not([class*=woocommerce-invalid]):not(.fc-no-validation-icon) .select2-selection:after, body.theme-oceanwp div.woocommerce form .form-row.woocommerce-validated:not([class*=woocommerce-invalid]):not(.fc-no-validation-icon):not(.fc-select2-field) .woocommerce-input-wrapper:after {
  top: 8px;
}

body.theme-oceanwp div.woocommerce form .form-row span.select2-container {
  height: auto;
}

body.theme-oceanwp div.woocommerce form .form-row .select2-selection {
  height: auto;
  min-height: 38px;
  border-radius: 3px;
  line-height: 0;
}

body.theme-oceanwp div.woocommerce form .form-row .select2-selection__rendered {
  min-height: 38px;
  border-radius: 3px;
}

body.theme-oceanwp div.woocommerce form .form-row .select2-selection .select2-selection__clear,
body.theme-oceanwp div.woocommerce form .form-row .select2-selection .select2-selection__arrow {
  top: 0;
  margin-top: 5px;
  height: auto;
  min-height: 26px;
  line-height: 26px;
  transform: none;
}

body.theme-oceanwp div.woocommerce form .form-row .select2-selection--single {
  padding: 0;
}

body.theme-oceanwp div.woocommerce form .form-row .select2-selection--single .select2-selection__rendered {
  padding-top: 0;
  padding-bottom: 0;
  line-height: 38px;
}

body.theme-oceanwp div.woocommerce form .form-row .select2-selection--multiple {
  border-width: 1px;
}

body.theme-oceanwp div.woocommerce form .form-row .select2-selection--multiple .select2-selection__rendered {
  padding-bottom: 5px;
  min-height: 28px;
  line-height: 28px;
